import {
    mapState
} from 'vuex';
  export default {
    data () {
      return {
        code: ''
      }
    },
    onReady () {
      this.sendRequest()
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      async sendRequest(){
        try {
          const detail = await this.$request.post('user/invite-code', {
            header: {
							token: this.userInfo.token
						}
          });

          this.$common.checkStatus(detail.data.code)
          this.code = detail.data.data
        } catch (error) {
            console.error('error:', error);
        }
      },
      saveImg () {
        let _this = this;
        uni.showActionSheet({
            itemList: ['保存图片'],
            success: function (res) {
                console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
                if(res.tapIndex === 0){
                  uni.saveImageToPhotosAlbum({
                     filePath: _this.code.invite_qrcode,
                     success: function () {
                         uni.showToast({
                             title: '已成功保存邀请二维码至您的相册，快去分享吧',
                             icon: 'none'
                         });
                     }
                 });
                }
            },
            fail: function (res) {
                console.log(res.errMsg);
            }
        });
      },
      copy () {
				uni.setClipboardData({
						data: this.code.invite_code,
						success: function () {
							uni.showToast({
								title: '复制邀请码成功，快去分享给好友吧',
								icon: 'none'
							});
								// console.log('复制成功');
						}
				});
      }
    },
  }
